<template>
  <div class="container">
    <div class="form">
      <el-form label-width="120px" :inline="true" :model="fleetDeclare" ref="fleetDeclareForm">
        <el-form-item label="归属部门:" prop="ownOrg">
          <el-input v-model="fleetDeclare.ownOrg"></el-input>
        </el-form-item>
        <el-form-item label="投保人姓名:" prop="policyHolder">
          <el-input v-model="fleetDeclare.policyHolder"></el-input>
        </el-form-item>
        <el-form-item label="协议号:" prop="ownOrg">
          <el-input v-model="fleetDeclare.protocolNo"></el-input>
        </el-form-item>
        <el-form-item label="归属业务员:" prop="ownSalesMan">
          <el-input v-model="fleetDeclare.ownSalesMan"></el-input>
        </el-form-item>
        <el-form-item label="协议创建日期:" prop="createTime">
          <el-select v-model="fleetDeclare.createTime">
            <el-option value="000000" label="2018/6/25"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="续保状态:" prop="insuranceType">
          <el-checkbox-group v-model="fleetDeclare.insuranceType">
            <el-checkbox label="1" name="insuranceType">新保</el-checkbox>
            <el-checkbox label="2" name="insuranceType">续保</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="申报状态:" prop="declareFlag">
          <el-checkbox-group v-model="fleetDeclare.declareFlag">
            <el-checkbox label="1" name="declareFlag">未申报</el-checkbox>
            <el-checkbox label="2" name="declareFlag">待审批</el-checkbox>
            <el-checkbox label="3" name="declareFlag">已审批</el-checkbox>
            <el-checkbox label="4" name="declareFlag">待修改</el-checkbox>
            <el-checkbox label="5" name="declareFlag">需要重新申报</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </div>
    <div class="option">
      <el-button type="primary" size="small" plain @click="resetForm('fleetDeclareForm')">重置</el-button>
      <el-button type="primary" size="small" plain>查询</el-button>
    </div>
    <div class="data">
      <div class="table">
        <el-table  style="width: 100%"  :data="currentData"  stripe :header-cell-style="tableHeader">
          <template v-for="column in columns">
            <el-table-column
              header-align="center"
              v-if="column.prop!=='declareFlag'"
              :key="column.prop"
              :prop="column.prop"
              :min-width="column.width"
              :label="column.title"
              :align="column.align">
            </el-table-column>
            <el-table-column
              header-align="center"
              v-else
              :key="column.prop"
              :min-width="column.width"
              :label="column.title"
              :align="column.align">
              <template slot-scope="scope">
                <el-tag v-if="scope.row.declareFlag==='待审批' " type="danger">{{scope.row.declareFlag}}</el-tag>
                <el-tag v-else-if="scope.row.declareFlag==='已审批' " type="success">{{scope.row.declareFlag}}</el-tag>
                <el-tag v-else>{{scope.row.declareFlag}}</el-tag>
              </template>
            </el-table-column>
          </template>
          <el-table-column header-align="center"  align="center" label="操作" min-width="150">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="modify(scope.row.protocolNo)">修改</el-button>
              <el-button type="text" size="small">查看</el-button>
            </template>
          </el-table-column>
          <el-table-column header-align="center"  aligin="center" label="配置多个归属业务员" min-width="200">
            <template slot-scope="scope">
              <el-select v-model="isConfig">
                <el-option value="是" label="是"></el-option>
                <el-option value="否" label="否"></el-option>
              </el-select>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <communal-pagination
          @search="search"
          :currentPage="currentPage"
          :pageSize="pageSize"
          :total="data.length">
      </communal-pagination>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
const CommunalPagination = () => import('@/components/CommunalPagination')

export default {
  name: 'list',
  components: {CommunalPagination},
  data () {
    return {
      columns: [
        {prop: 'protocolNo', title: '协议号', width: 240, align: 'center'},
        {prop: 'ownOrg', title: '归属机构', width: 120, align: 'center'},
        {prop: 'policyHolder', title: '投保人', width: 240, align: 'center'},
        {prop: 'protocolCount', title: '协议车辆数', width: 120, align: 'center'},
        {prop: 'createTime', title: '协议创建时间', width: 120, align: 'center'},
        {prop: 'insuranceType', title: '续保标志', width: 120, align: 'center'},
        {prop: 'declareFlag', title: '申报状态', width: 120, align: 'center'},
        {prop: 'level', title: '级别', width: 120, align: 'center'},
        {prop: 'operator', title: '处理人', width: 120, align: 'center'}

      ],
      data: [
        {
          protocolNo: '805012015110000004409',
          ownOrg: '02660600',
          policyHolder: '同顺运输有限公司',
          protocolCount: 136,
          createTime: '2018-02-23',
          insuranceType: '新保',
          declareFlag: '待审批',
          level: '初审',
          operator: '张三'
        },
        {
          protocolNo: '805012015110000004721',
          ownOrg: '02660601',
          policyHolder: '欣舜物流有限公司',
          protocolCount: 152,
          createTime: '2018-02-23',
          insuranceType: '被续保',
          declareFlag: '已审批',
          level: '初审',
          operator: '张三'
        },
        {
          protocolNo: '805012015110000007614',
          ownOrg: '02660602',
          policyHolder: '天元人力资源有限公司',
          protocolCount: 100,
          createTime: '2018-02-23',
          insuranceType: '新保',
          declareFlag: '未申报',
          level: '初审',
          operator: '张三'
        },
        {
          protocolNo: '805012015110000011460',
          ownOrg: '02660603',
          policyHolder: '德辉物流有限公司',
          protocolCount: 123,
          createTime: '2018-02-23',
          insuranceType: '新保',
          declareFlag: '待修改',
          level: '初审',
          operator: '张三'
        },
        {
          protocolNo: '805012015110000012807',
          ownOrg: '02660604',
          policyHolder: '莱芜市公安局',
          protocolCount: 200,
          createTime: '2018-02-23',
          insuranceType: '续保',
          declareFlag: '需要重新申报',
          level: '续保',
          operator: '张三'
        }
      ],
      fleetDeclare: {
        ownOrg: '',
        policyHolder: '',
        protocolNo: '',
        ownSalesMan: '',
        createTime: '',
        insuranceType: [],
        declareFlag: []
      },
      isConfig: '否',
      currentPage: 1,
      pageSize: 6,
      total: 0
    }
  },
  created () {
    console.log(this.$refs)
  },
  computed: {
    currentData () {
      const pageData = []
      const start = (this.currentPage - 1) * this.pageSize
      let end = this.currentPage * this.pageSize
      end = end <= this.data.length ? this.currentPage * this.pageSize : this.data.length
      for (let i = start; i < end; ++i) {
        pageData.push(this.data[i])
      }
      return pageData
    },
    ...mapState({tableHeader: state => state.app.tableHeader})
  },
  methods: {
    modify (protocolNo) {
      this.$router.push('/admin/fleet/declare/modify')
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    search (pageNumber, pageSize) {
      this.currentPage = pageNumber
      this.pageSize = pageSize
      console.log(pageNumber)
    }
  }
}
</script>

<style scoped>

</style>



